body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'adriane', Georgia, serif;
}

.App-body {
  background-color: white;
  border: 10px solid #004D71;
  margin: 0px;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: #004D71;
}

.App-link {
  color: #61dafb;
}

.menu-container {
  float: right;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: white;
  padding-top: 2em;
  padding-bottom: 2em;
  font-size: 70%;
}

li {
  float: left;
}

li a {
  display: block;
  color: #494c4e;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover {
  background-color: #494c4e;
  color: white;
}

.mobile_nav {
  color: black;
}


/* Fixed/sticky icon bar (vertically aligned 50% from the top of the screen) */
.icon-bar {
    position: fixed;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  
  /* Style the icon bar links */
  .icon-bar a {
    display: block;
    text-align: center;
    padding: 16px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    color: none;
    font-size: 20px;
  }
  
  /* Style the social media icons with color, if you want */
  .icon-bar a:hover {
    background-color: black;
    color: white;
  }

  .linkedin {
    background: #2274AF;
    color: white;
  }  
  
  .github {
    background: #24292E;
    color: white;
  }

  .contact {
    background: #494C4E;
    color: white;
  }


  
img {
    display: block;
    width: 70%;
    height: auto;
    margin-top: 2em;
    margin-bottom: 2em;
    background-color: #f0eeee;
    margin-left: auto;
    margin-right: auto;
}
.container {
    display: -webkit-flex;
    display: flex;
}

.title {
    -webkit-order: 1;
            order: 1
}

.content {
    -webkit-order: 2;
            order: 2;
    padding-left: 2em;
    padding-right: 2em;
}
.container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

