    padding: 2rem;

}

.project2{
    padding: 2rem;
   
}

@media only screen and (max-width: 600px) {
    .container {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
}
